import React from 'react';
import T from 'i18n-react'
import { Link } from 'react-router-dom';
import { Hero, HeroBody, HeroFooter, Container, Box, Columns, Column, Image, Button } from "bloomer";
import FloatLabelnput from "../components/FloatLabelnput";
import Footer from "../components/Footer";

import globalStorage from 'react-global-storage';
import alertify from 'alertify.js'

import "../assets/styles/page-login.css";

import logo from "../assets/images/SVG/capitual-logo.svg";

import cap from '../lib/capsdk'

import { detectLanguage } from '../helper/i18n'

class SignupView extends React.Component {

  state = {
    email: null
  }

  constructor(props) {
    super(props);
  }

  onChange = (e) => {
      e.preventDefault();
      const { name, value } = e.target;
      this.setState({ [name]:value })
  }

  createAccountSubmit = async(e) => {
    e.preventDefault()
    let locale = detectLanguage()
    let email = this.state.email

    // try registering
    try {
      let sess_key = await cap.requestA('members.register', {
        email: email,
        locale: locale
      })
      localStorage.setItem('sess_key', sess_key)
      alertify.success(T.translate("pages.signup.success"));
      this.props.history.push('/dashboard')
    } catch(e) {
      let gotoRecover
      switch (e) {
        case 'InvalidEmailAddress':
          alertify.okBtn(T.translate("global.labels.ok")).alert(T.translate("pages.signup.emailError"))
          break
        case 'EmailExists':
           gotoRecover = await alertify
            .okBtn(T.translate("pages.signup.recoverPwd"))
            .cancelBtn(T.translate("pages.signup.tryAgain"))
            .confirm(T.translate("pages.signup.emailExists"))
          if (gotoRecover=='ok') {
            this.props.history.push('/recover')
          }
          break
        case 'PhoneExists':
           gotoRecover = await alertify
            .okBtn(T.translate("pages.signup.recoverPwd"))
            .cancelBtn(T.translate("pages.signup.tryAgain"))
            .confirm(T.translate("pages.signup.phoneExists"))
          if (gotoRecover=='ok') {
            this.props.history.push('/recover')
          }
          break
        case 'NeedEmailOrPhone':
          alertify.okBtn(T.translate("global.labels.ok")).alert(T.translate("pages.signup.emailError"))
          break
      }
    }
  }

  render() {
    return (
        <Hero className="page-login" isFullHeight>
            <HeroBody>
                <Container>
                    <Columns isCentered>
                        <Column isSize={4} isMarginless>
                          <form onSubmit={this.createAccountSubmit}>
                            <Box>
                                <Columns isMultiline>
                                    <Column isSize="auto">
                                        <Link to="/">
                                            <Image src={logo} isSize="64x64" hasTextAlign="centered" style={{margin: '0 auto'}} />
                                        </Link>
                                    </Column>
                                    <br/>
                                    <Column isSize={12}>
                                        <FloatLabelnput label={ T.translate("pages.signup.usernameInput") } type="email" name="email" onChange={this.onChange} required />
                                    </Column>

                                    <Column isSize={12}>
                                        <Button isColor="primary" isSize="large" isFullWidth type="submit">{ T.translate("pages.signup.submitBtn") }</Button>
                                    </Column>
                                    <Column>
                                        <p className="has-text-centered">{ T.translate("pages.signup.alreadyRegistered") } <Link to="/login">{ T.translate("pages.signup.signIn") }</Link></p>
                                    </Column>
                                </Columns>
                              </Box>
                            </form>
                        </Column>
                    </Columns>
                </Container>
            </HeroBody>
            <HeroFooter>
                <Footer />
            </HeroFooter>
        </Hero>
    );
  }
}

export default SignupView;



